<template>
  <div class="bg-gray-100 p-6">
    <h1 class="dashboard__title text-center mx-auto">U≈ºytkownicy</h1>
    <div class="max-w-4xl mx-auto bg-white rounded-lg shadow-md p-6">
      <div class="user-grid text-center font-bold py-2 border-b-2 mb-4">
        <div>ID</div>
        <div>Nazwa u≈ºytkownika</div>
        <div>ID Karty RFID</div>
        <div>Akcje</div>
      </div>
      <ul>
        <li v-for="user in users" :key="user.id" class="user-grid text-center items-center p-2 hover:bg-gray-50">
          <span>{{ user.id }}</span>
          <span>{{ user.username }}</span>
          <span>{{ user.rfid }}</span>
          <button v-if="user.id !== 1" @click="deleteUser(user.id)" class="py-1 px-3 bg-red-700 hover:bg-red-800 rounded text-white">Usu≈Ñ</button>
        </li>
      </ul>
      <div class="mt-4">
        <input v-model="newUser.username" placeholder="Nazwa u≈ºytkownika" class="border p-2 rounded w-full mb-4">
        <button @click="addUser" class="btn__alert--submit py-2 px-4 rounded focus:outline-none focus:shadow-outline">Dodaj u≈ºytkownika</button>
      </div>
    </div>
  </div>
</template>

<style>
.user-grid {
  display: grid;
  grid-template-columns: 0.5fr 1.5fr 1fr 0.5fr;
}
</style>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      users: [],
      newUser: {
        username: ''
      }
    };
  },
  methods: {
    fetchUsers() {
      axios.get('/users')
        .then(response => {
          this.users = response.data;
        })
        .catch(error => {
          console.error('B≈ÇƒÖd pobierania u≈ºytkownik√≥w:', error);
          alert('B≈ÇƒÖd pobierania u≈ºytkownik√≥w: ' + error.message);
        });
    },
    addUser() {
      if (!this.newUser.username.trim()) {
        alert('Nazwa u≈ºytkownika jest wymagana');
        return;
      }

      alert('Proszƒô przy≈Ço≈ºyƒá kartƒô do czytnika...');
      axios.post('/add-user', this.newUser)
        .then(() => {
          alert('U≈ºytkownik dodany!');
          this.fetchUsers();
          this.newUser.username = '';
          window.dispatchEvent(new Event('userUpdated'));
        })
        .catch(error => {
          const errorMessage = error.response && error.response.data && error.response.data.error
            ? error.response.data.error
            : 'Nieznany b≈ÇƒÖd';
          alert('B≈ÇƒÖd dodawania u≈ºytkownika: ' + errorMessage);
        });
    },
    deleteUser(id) {
      axios.delete(`/user/${id}`)
        .then(() => {
          alert('U≈ºytkownik usuniƒôty');
          this.fetchUsers();
          window.dispatchEvent(new Event('userUpdated'));
        })
        .catch(error => {
          alert('B≈ÇƒÖd usuwania u≈ºytkownika: ' + error.response.data.error);
        });
    }
  },
  mounted() {
    this.fetchUsers();
  }
}
</script>